import {
	basePath
} from '@/utils/index'
import api from '@/api/order';
import {
	mapState
} from 'vuex'

import tuiTabs from "@/components/tui-tabs/tui-tabs"
import tuiButton from "@/components/extend/button/button"
import tuiLoadmore from "@/components/loadmore/loadmore"
import tuiNomore from "@/components/nomore/nomore"
import tuiListCell from "@/components/list-cell/list-cell"
export default {
	components: {
		tuiTabs,
		tuiButton,
		tuiLoadmore,
		tuiNomore,
		tuiListCell
	},
	data() {
		return {
			basePath,
			tabs: [{
				name: "全部"
			}, {
				name: "待付款"
			}, {
				name: "待发货"
			}, {
				name: "已发货"
			}, {
				name: "已完成"
			}],
			orderList: [],
			currentTab: 0,
			pageIndex: 1,
			lastPage:1,
			loadding: false,
			pullUpOn: true,
			scrollTop: 0
		}
	},
	computed: mapState(['forcedLogin', 'hasLogin', 'member_id', 'openid']),
	onLoad() {
		this.getOrder()
	},
	methods: {
		getGoodsNum(list){
			console.log(JSON.stringify(list))
			if(list&&list.length>0){
				let goodsNum = 0;
				list.map(item => {
					goodsNum += item.qty;
				})
				console.log("数量"+goodsNum)
				return goodsNum
			}
		},
		getGoodsTotal(list){
			var amount=0;
			list.map(item => {
				amount += item.qty*item.price;
			})
			console.log("金额"+amount)
			return this.$returnFloat(amount)
		},
		//获取全部收货地址
		getOrder() {
			var param = {
				page: this.$pagination.page,
				limit: this.$pagination.limit,
				status: this.currentTab == 0 ? '' : this.currentTab
			}
			var url = api.getOrder + '/' + this.member_id;
			this.$postajax(url, param)
				.then(res => {
					console.log(JSON.stringify(res))
					if (res.code == 0) {
						this.orderList = [];
						if (res.data && res.data.length > 0) {
							res.data.forEach(item => {
								item.data = JSON.parse(item.data)
								this.orderList.push(item)
							})
							this.lastPage =res.data&&res.data.length>0?(Math.ceil(res.count/this.$pagination.limit)):1;
							console.log("最后一页"+JSON.stringify(this.lastPage))
							if(this.pageIndex==this.lastPage){
								this.pullUpOn = false;
							}
							this.count=res.count;
						}
					}
				})
				.catch(err => {

				});
		},
		//切换订单状态
		change(e) {
			this.currentTab = e.index;
			this.getOrder();
		},
		//查看订单详情
		detail(id) {
			uni.navigateTo({
				url: '../orderDetail/orderDetail?id=' + id
			})
		},
		//取消订单
		cancelOrder(id) {
			var url = api.closeOrder + '/' + id;
			this.$postajax(url)
				.then(res => {
					if (res.code == 0) {
						this.tui.toast("订单取消成功", 2000, true);
						uni.navigateTo({
							url: "../success/success?orderStatus=2"
						})
					}
				})
		},
		//再次购买
		repurchase(id) {
			uni.navigateTo({
				url: '../productDetail/productDetail?id=' + id
			})
		},
		//立即支付
		payOrder(id) {
			console.log("支付信息" + JSON.stringify(id))
			var url = api.payOrder + '/' + id;
			this.$postajax(url)
				.then(res => {
					console.log("支付" + JSON.stringify(res))
					if (res.code == 0) {
						let orderInfo = res.data;
						//微信支付

						uni.requestPayment({

							provider: 'wxpay',

							orderInfo: orderInfo, //订单数据
							timeStamp: orderInfo.timeStamp,
							nonceStr: orderInfo.nonceStr,
							package: orderInfo.package,
							signType: orderInfo.signType,
							paySign: orderInfo.paySign,
							service: 3,

							success: function(res) {

								//处理业务逻辑
								this.tui.toast("提交订单成功", 2000, true);
								uni.navigateTo({
									url: "../success/success?orderStatus=2"
								})

							},

							fail: function(err) {

								console.log('fail:' + JSON.stringify(err));
								uni.showToast({

									icon: 'none',

									title: '支付失败'

								});

							}

						});
					}
				})
				.catch(err => {

				});
		}
	},
	onPullDownRefresh() {
		setTimeout(() => {
			uni.stopPullDownRefresh()
		}, 200);
	},
	onReachBottom: function() {
		console.log(3333)
		/* if (!this.pullUpOn) return; */
		this.pullUpOn = true
		this.loadding = true;
		console.log('页面上拉触底事件');
		// 每次拉到页面底部,请求页数自加
		this.pageIndex++;
		// 第二次之后请求数据
		if(this.pageIndex>this.lastPage){
			setTimeout(() => {
				this.loadding = false
				this.pullUpOn = false
			}, 1000)
			return false;
		}else{
		var param = {
			page: this.pageIndex,
			limit: this.$pagination.limit,
			member_id:this.member_id,
			name: this.searchKey,
		}
		this.$postajax(api.getProduct,param)
			.then(res => {
				console.log(JSON.stringify(res))
				if (res.code == 0) {
					console.log(JSON.stringify(res))
					if (res.code == 0) {
						res.data=res.data.map(item => {
							item.data = JSON.parse(item.data)
							return item
						})
						this.orderList = this.orderList.concat(res.data)
						this.loadding = false;
					}
				}
		
		
			})
			.catch(err => {
		
			});
		}
	},
	onPageScroll(e) {
		this.scrollTop = e.scrollTop;
	}
}
